body {
    height: 100vh;
    overflow-x: hidden;
    background: linear-gradient(to right, rgb(52, 241, 255), rgb(44, 149, 209));
}



.users {
    background-color: #456360;
    height:70px;
    color: #f3f3f3;
    display: flex;
    transition: 0.5s;
}

.users h3 {
    margin-left: 1%;
}


.user {
    text-align: center;
    width: 80%;
}

.UsernameButton {
    margin-top: 25px;
    border-radius: 5px;
    border: none;
    height: 25px;
    width: 10%;
    color: black;
    background-color:rgb(255 255 255);
    text-transform: capitalize;
    transition: 0.5s;
    font-size: 18px;
    font-weight: 400;
}

.msgBox {
    transition: 1s;
    margin-top: 10px;
    /* border-top: 2px solid black; */
    padding-top: 10px;
    text-align: center;
    justify-content: center;
}

.textmsg {
    display: flex;
    margin-left: 2%;
    justify-content: center;
}

.textmsg .author {
    text-transform: capitalize;
    border: 1px solid black;
    border-radius: 5px;
    text-align: center;
    text-justify:distribute;
    background-color: rgb(96, 100, 100);
    color: wheat;
}

.textmsg .value {
    margin-left: 2%;
    border: 1px solid black;
    border-radius: 5px;
    background-color: rgba(57, 179, 32, 0.856);
    color: white;
    font-weight: 500;
    height: 35px;
    text-align: start;
    padding-left: 1%;
    font-size: 17px;
    padding-top: 2px;
    padding-bottom: 2px;
    padding-right: 1%;
}

.mms .author {
    margin-left: 15px;
}

.mms {
    margin-left: 50%;
}

.sms {
    margin-right: 50%;
}

.date {
    font-size: 10px;
    text-align: end;

    margin-top: 25px;
}

.hidden {
    display: none;
}



.sendfield {
    transition: 0.5s;
    width: 70%;
    height: 41px;
    border: 2px solid black;
    border-radius: 15px 0px 0px 15px;
    outline: none;
    text-align: center;
    background-color: rgb(28, 255, 198);
    color: black;
    margin-bottom: 20vh;
}

.sendbtn {
    transition: 0.5s;
    height: 41px;
    border-radius: 0px 15px 15px 0px;
    outline: none;
    border: 2px solid black;
    color: rgb(0, 0, 0);
    background-color: rgb(0, 255, 136);
    border-left: none;
}

.author {
    width: 12%;
    height: 30px;
}

.date {
    border: 1px solid blue;
    height: 18px;
    width: 12%;
    text-align: center;
    background-color: blue;
    opacity: 0.1;
    border-radius: 15px;
    color: white;
    cursor: default;
}

.date:hover {
    opacity: 1;
}

.sms .value {
    margin-right: 10px;
} 

.msgBox {
    overflow-y: auto;
    height: 60vh;
}